import React, { useState, useEffect } from "react";
import dayjs from "dayjs";
import "dayjs/locale/es"; // Importamos el idioma español
import utc from "dayjs/plugin/utc";
import timezone from "dayjs/plugin/timezone";

// Importamos los estilos de Materialize CSS
import "materialize-css/dist/css/materialize.min.css";
import "./App.css"; // Importamos nuestro archivo de estilos personalizado

// Agregamos los plugins de utc y timezone
dayjs.extend(utc);
dayjs.extend(timezone);

function App() {
  const [birthday, setBirthday] = useState("");
  const [age, setAge] = useState(null);
  const [totalTime, setTotalTime] = useState(null);
  const [nextBirthday, setNextBirthday] = useState(null);

  const handleCalculateAge = () => {
    if (!birthday) return;

    const today = dayjs();
    const birthDate = dayjs(birthday);

    const calculatedAge = today.diff(birthDate, "year");
    setAge(calculatedAge);

    const totalYears = today.diff(birthDate, "year");
    const totalMonths = today.diff(birthDate, "month");
    const totalDays = today.diff(birthDate, "day");
    const totalHours = today.diff(birthDate, "hour");

    setTotalTime({
      years: totalYears,
      months: totalMonths,
      days: totalDays,
      hours: totalHours,
    });

    calculateNextBirthday(today, birthDate);
  };

  const calculateNextBirthday = (today, birthDate) => {
    const nextBirthdayDate = birthDate.add(age + 1, "year");
    const daysUntilNextBirthday = nextBirthdayDate.diff(today, "day");

    setNextBirthday({
      date: nextBirthdayDate,
      daysUntil: daysUntilNextBirthday,
    });
  };

  useEffect(() => {
    if (age !== null) {
      const today = dayjs();
      const birthDate = dayjs(birthday);
      calculateNextBirthday(today, birthDate);
    }
  }, [age, birthday]);

  return (
    <div className="container">
      <div className="row">
        <div className="col s12">
          <h4>Fecha Actual</h4>
          <p className="current-date-large">
            {dayjs().locale('es').format("D [de] MMMM [de] YYYY")}
          </p>
        </div>
      </div>
      <h1 className="center-align">Calculadora de Edad</h1>
      <div className="row">
        <div className="input-field col s12">
          <input
            type="date"
            value={birthday}
            onChange={(e) => setBirthday(e.target.value)}
            className="datepicker"
          />
          <label htmlFor="date">Fecha de Nacimiento</label>
        </div>
        <div className="col s12 center-align">
          <button
            className="btn waves-effect waves-light"
            onClick={handleCalculateAge}
          >
            <i className="material-icons right">Calcular Edad</i>
          </button>
        </div>
      </div>
      {age !== null && (
        <div className="row">
          <div className="col s12 center-align">
            <p className="age-result">Tienes {age} años.</p>
          </div>
        </div>
      )}

      {totalTime && (
        <div className="row">
          <div className="col s12 center-align">
            <h4>Tiempo Total Vivido</h4>
            <p>Meses: {totalTime.months} </p>
            <p>Dias: {totalTime.days}</p>
            <p>Horas: {totalTime.hours}</p>
          </div>
        </div>
      )}

      {nextBirthday && (
        <div className="row">
          <div className="col s12 center-align">
            <h4>Próximo Cumpleaños</h4>
            <p>Tu próximo cumpleaños será en {nextBirthday.daysUntil} días.</p>
          </div>
        </div>
      )}

      <div className="row">
        <div className="col s12">
          <h4>Fecha en diferentes zonas horarias</h4>
          <div className="zone">
            <p>
              México:{" "}
              {dayjs()
                .tz("America/Mexico_City")
                .locale('es')
                .format("D [de] MMMM [de] YYYY, h:mm A")}
            </p>
            <p>
              Nueva York:{" "}
              {dayjs()
                .tz("America/New_York")
                .locale('es')
                .format("D [de] MMMM [de] YYYY, h:mm A")}
            </p>
            <p>
              Tokio:{" "}
              {dayjs()
                .tz("Asia/Tokyo")
                .locale('es')
                .format("D [de] MMMM [de] YYYY, h:mm A")}
            </p>
            <p>
              Londres:{" "}
              {dayjs()
                .tz("Europe/London")
                .locale('es')
                .format("D [de] MMMM [de] YYYY, h:mm A")}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
